import React, { useState } from 'react'

import '../assets/scss/Governance.scss'
import ArrowDropDownIcon from '../assets/imgs/ArrowDropDownIcon.svg'
import ArrowDropUpIcon from '../assets/imgs/ArrowDropUpIcon.svg'
import ThreeDots from '../assets/imgs/ThreeDots.svg'
import User from '../assets/imgs/User.svg'
import LeftArrow from '../assets/imgs/ArrowLeftIcon.svg'
import ExternalLink from '../assets/imgs/ExternalLinkIcon.svg'
import Confirm from '../assets/imgs/ConfirmIcon.svg'

import Approve from '../assets/imgs/Approve.svg'
import Deny from '../assets/imgs/Deny.svg'
import SortDown from '../assets/imgs/ArrowDropDownIcon.svg'
import SortUp from '../assets/imgs/ArrowDropUpIcon.svg'

import Modal from '../components/Modal'

import { conviction_date, table_proposals, proposals_date, table_data, table_approved } from '../config/variables'
import { useHistory } from 'react-router-dom'

const Governance = () => {
  const history = useHistory()
  const [isSort, setIsSort] = useState(true)

  const [status, setStatus] = useState('home')

  const [currentTable, setCurrentTable] = useState();

  const [isAgainst, setIsAgainst] = useState(false)

  const [isOpen, setIsOpen] = useState(false)

  const [title, setTitle] = useState('')

  const [submittedAddress, setSubmittedAddress] = useState('')
  const [submittedDate, setSubmittedDate] = useState(() => {
    let rightNow = new Date();
    let res = rightNow.toISOString().slice(0, 10);
    return res
  })

  const handleCallback = (event, fromEvent, proposal) => {
    if(event === 'go to events') history.push('./events');
    setStatus(event)
    setCurrentTable(proposal)
  }

  return (
    <div className='governance'>
      {
        status === 'home' && <>
          <div className='row'>
            <div className='proposals col-lg bg-card border-05 radius-10 p-20 mb-10'>
              <div className='d-flex justify-content-between bottom-line'>
                <p className='fs-21 title-color'>Recent Proposals</p>
                <div>
                  <p className='fs-18 text-color'></p>
                </div>
              </div>
              <table className="table table-hover">
                <tbody>
                  {
                    proposals_date.map((proposal, index) => (
                      <tr key={index}>
                        <td>
                          <p className='fs-16 title-color mb-1'>{proposal.name}</p>
                          <p className='fs-11 text-color mb-0'>Submitted {proposal.created_at}<span className='voting'>Voting</span></p>
                        </td>
                        <td>
                          <p
                            className="pri-color mb-0 cursor-point"
                            onClick={() => handleCallback("proposal", 'all', proposal)}
                          >
                            View
                          </p>
                        </td>
                        <td>
                          <p className='my-auto pri-color'>In Progress</p>
                        </td>
                      </tr>
                    ))}                 
                </tbody>
              </table>
              <div className='view-all fs-16 title-color cursor-point' onClick={() => setStatus('all')}>View All Proposals</div>
            </div>
          </div>
          <div className='proposals col-lg bg-card border-05 radius-10 p-20 mb-10'>
            <div className='d-flex justify-content-between bottom-line'>
              <p className='fs-21 title-color'>Approved Events</p>
              <div>
                <p className='fs-18 text-color'></p>
              </div>
            </div>
            <table className="table table-hover">
              <tbody>
                {table_approved.map((t, index) => (
                  <tr key={index}>
                    <td>
                      <img src={t.logo} alt="" className="mr-10" />
                      {t.project}
                    </td>
                    <td onClick={() => console.log("abc")}>
                      <p
                        className="pri-color mb-0 cursor-point"
                        onClick={() => handleCallback('go to events')}
                      >
                        View
                      </p>
                    </td>
                    <td>{t.created_at}</td>
                    <td>{t.claim_status ? "Claimable" : "Not Claimable"}</td>
                    <td>
                      <img
                        src={
                          t.csr_status === 0
                            ? ThreeDots
                            : t.csr_status === 1
                              ? Deny
                              : Approve
                        }
                        alt=""
                        className="mr-10"
                      />
                      {t.csr_status === 0
                        ? "Pending"
                        : t.csr_status === 1
                          ? "Denied"
                          : "Approved"}
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
            <div className='view-all fs-16 title-color cursor-point' onClick={() => setStatus('all')}>View All Approved Events</div>
          </div>
          <div className='mt-10 bg-card border-05 radius-10 p-20 proposal'>
            <p className='fs-21 title-color semi-bold'>Submit a proposal</p>
            <p>To submit a governance proposal, you will need to be a member, staker and have a conviction score of more than 1% of the total leaderboard.</p>
            <div className='wrapper'>
              <p className='title-color semi-bold'>How can I become a governance member for the network?</p>
              <p>Everyone has an opportunity to earn a position of liquid governance. Contributors, who are also members, that have reached and maintain a preset conviction score threshold are eligible for liquid governance.</p>
            </div>
            <div className='wrapper'>
              <p className='title-color semi-bold'>Why does FairSide need a guardian of the DAO?</p>
              <p>The guardian of the DAO has certain managerial functions, such as the ability to immigrate cryptographically provable off-chain votes on-chain, in order to save on gas costs for the members. The existing system will permit a vote on-chain to kick out the guardian when this function is no longer needed.</p>
            </div>
            <div className='wrapper'>
              <p className='title-color semi-bold'>What requirements are there to govern the network and earn additional rewards?</p>
              <p>To be eligible for governance and the additional governance rewards, you must meet the current conviction score threshold determined by the DAO. Contributors who join with a certain contribution level during the Hatch Phase will automatically be granted governance level.</p>
            </div>
            <div className='d-flex justify-content-between'>
              <p className='fs-14'>To learn more visit: <span className='pri-color'>Visit our FAQ</span></p>
              <button className='radius-10 border-05 bg-card' onClick={() => setStatus('submit')}>Create Proposal</button>
            </div>
          </div>
        </>
      }
      {
        status === 'all' && <>
          <div className='proposals'>
            <div className='header'>
              <div className='d-flex flex-column'>
                <p className='fs-31 mb-10 title-color'>Proposals</p>
                <p>View and vote on proposals</p>
              </div>
              <div>
                <button onClick={() => setStatus('submit')}>Create Proposals</button>
              </div>
            </div>
            <div className='fairside-table table-responsive p-3 border-05 radius-10'>
              <table className='table table-hover'>
                <thead>
                  <tr>
                    <td>Project</td>
                    <td>Action</td>
                    <td onClick={() => setIsSort(!isSort)} className='cursor-point'>Status <img src={isSort ? SortDown : SortUp} alt='' className='ml-5' /></td>
                  </tr>
                </thead>
                <tbody>
                  {
                    table_proposals.map((t, index) => (
                      <tr key={index}>
                        <td><img src={t.logo} alt='' className='mr-10' />{t.project}</td>
                        <td onClick={() => console.log('abc')}><p className='pri-color mb-0 cursor-point' onClick={() => setStatus('proposal')}>View</p></td>
                        <td><img src={Approve} alt='' className='mr-10' />Executed</td>
                      </tr>
                    ))
                  }
                </tbody>
              </table>
            </div>
          </div>
        </>
      }

      {
        status === 'proposal' && <>
          <div>
            <div className='d-flex flex-column'>
              <p className='fs-31 mb-10 title-color'>Proposals</p>
              <p>Vote on Proposals to decide the outcome</p>
            </div>
            <div className='fairside-table bg-card p-3 border-05 radius-10'>
              <div className='p-20'>
                <div className='d-flex flex-row mb-30'>
                  <img src={LeftArrow} alt='' className='cursor-point' onClick={() => setStatus('all')} />
                  <div className='d-flex justify-content-center w-100 fs-26 title-color semi-bold'>
                    Increase Bug Bounty
                  </div>
                </div>
                <div className='row'>
                  <div className='col-lg-7 f-card-body mr-30'>
                    <p className='fs-21 title-color semi-bold mb-30'>Details</p>
                    <div className='d-flex flex-row'>
                      <p className='fs-16 semi-bold title-color mr-10'>Affected Address:</p>
                      <p>0x3c16183c1C0E28F1a0cB9F8ee4b21D0Db208cA46 <a rel='noreferrer' href='https://etherscan.io/address/0x3c16183c1C0E28F1a0cB9F8ee4b21D0Db208cA46' target='_blank' ><img src={ExternalLink} alt='' className='ml-10' /></a></p>
                    </div>
                    <div className='d-flex flex-row'>
                      <div className='d-flex flex-row mr-30'>
                        <p className='fs-16 semi-bold title-color mr-10'>Asset Lost</p>
                        <p>20 ETH</p>
                      </div>
                      <div className='d-flex flex-row'>
                        <p className='fs-16 semi-bold title-color mr-10'>Coverage</p>
                        <p>10 ETH</p>
                      </div>
                    </div>
                    <div className='mb-30'>
                      Unusual activity has been reported regarding the distribution of COMP following the execution of Proposal 062.
                      No supplied/borrowed funds are at risk -- Compound Labs and members of the community are investigating discrepancies in the COMP distribution.
                    </div>
                    <p className='fs-21 title-color semi-bold mb-10'>Requirements</p>
                    <div className='mb-30'>
                      Provide time stamps, links and transaction ID for every transaction made following up to exploit.
                    </div>
                    <p className='fs-21 title-color semi-bold mb-10'>Assessment</p>
                    <div className='mb-30'>
                      Based on further assessment techniques, we will be providing and uploading all the data that will be used.
                    </div>
                  </div>
                  <div className='col-lg f-card-body mb-md-2'>
                    <div className='d-flex justify-content-between flex-row'>
                      <p className='fs-21 title-color semi-bold mb-10'>For</p>
                      <p className='fs-21 title-color mb-10'>44, 234</p>
                    </div>
                    <div className='mb-10'>
                      <div className='progress'>
                        <div className='progress-bar' style={{ 'width': '74%' }}></div>
                      </div>
                    </div>
                    <div className='d-flex justify-content-between flex-row'>
                      <p className='fs-16'>20 Addresses</p>
                      <p className='fs-16'>20 Addresses</p>
                    </div>
                    <div className='d-flex justify-content-between flex-row mb-30'>
                      <p className='fs-16'>0x3c1...cA46</p>
                      <p className='fs-16'>0x3c1...cA46</p>
                    </div>
                    <div className='d-flex justify-content-between flex-row'>
                      <p className='fs-21 title-color semi-bold mb-10'>Against</p>
                      <p className='fs-21 title-color mb-10'>18, 234</p>
                    </div>
                    <div className='mb-10'>
                      <div className='progress'>
                        <div className='progress-bar' style={{ 'width': '26%' }}></div>
                      </div>
                    </div>
                    <div className='d-flex justify-content-between flex-row'>
                      <p className='fs-16'>20 Addresses</p>
                      <p className='fs-16'>Votes</p>
                    </div>
                    <div className='d-flex justify-content-between flex-row'>
                      <p className='fs-16'>0x3c1...cA46</p>
                      <p className='fs-16'>6,124</p>
                    </div>
                    <div className='bottom-line mb-30'></div>
                    <div className='d-flex flex-row justify-content-around mb-30'>
                      <div className='d-flex flex-column align-items-center'>
                        <p>For Proposal</p>
                        <div className={`cursor-point option ${!isAgainst ? 'active' : ''}`} onClick={() => setIsAgainst(!isAgainst)} />
                      </div>
                      <div className='d-flex flex-column align-items-center'>
                        <p>Against Proposal</p>
                        <div className={`cursor-point option ${isAgainst ? 'active' : ''}`} onClick={() => setIsAgainst(!isAgainst)} />
                      </div>
                    </div>
                    <div className='d-flex justify-content-center'>
                      <button className='submit' onClick={() => setIsOpen(true)}>Submit</button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </>
      }
      {
        status === 'submit' && <>
          <div className='d-flex flex-column'>
            <p className='fs-31 mb-10 title-color'>Submit Proposal</p>
            <p>Provide details regarding proposal</p>
          </div>
          <div className='fairside-table bg-card p-20 border-05 radius-10'>
            <div>
              <div className='row mt-10'>
                <div className='col-md-4 mr-30'>
                  <p className='fs-18 title-color mb-10'>Title</p>
                  <p className='fs-14 mb-10'>Proposal title</p>
                  <input className='input-box' type='number' value={title} onChange={(e) => setTitle(e.target.value)} />
                </div>
              </div>
              <div className='row mt-30'>
                <div className='col-md-8 mr-30 textarea-wrapper'>
                  <textarea className='input-box textarea' placeholder='Provide additional info here' />
                </div>
              </div>
              <div className='mt-30 d-flex flex-row flex-wrap'>
                <div className='d-flex flex-column mr-30' style={{ 'width': '170px' }}>
                  <p className='fs-18 title-color mb-10'>Date Submitted</p>
                  <input type='date' className='input-box' value={submittedDate} onChange={(e) => setSubmittedDate(e.target.value)} />
                </div>
                <div className='d-flex flex-column' style={{ 'width': '250px' }}>
                  <p className='fs-18 title-color mb-10'>Address Submitting</p>
                  <input className='input-box' value={submittedAddress} onChange={(e) => setSubmittedAddress(e.target.value)} />
                </div>
              </div>
              <div className='d-flex justify-content-end'>
                <button className='submit'>Submit</button>
              </div>
            </div>
          </div>
        </>
      }
      {/* {
        status === 'vote-event' && <VoteEventTable fromStatus={'all'} data={currentTable} callback={handleCallback} />
      } */}
      <Modal isOpen={isOpen} setIsOpen={setIsOpen}>
        <div className='d-flex align-items-center flex-column p-20'>
          <img src={Confirm} alt='' className='mb-30' />
          <p className='text-color mb-0'>Your vote has been submitted.</p>
        </div>
      </Modal>
    </div>
  )
}

export default Governance